import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, Link, useIntl } from 'gatsby-plugin-intl';

const ContactPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const firm = allFile.edges.find((v) => v.node.name === 'firm').node.childImageSharp.fluid;
  const services = allFile.edges.find((v) => v.node.name === 'services').node.childImageSharp.fluid;
  const commitment = allFile.edges.find((v) => v.node.name === 'commitment').node.childImageSharp.fluid;

  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            <Row></Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            <Row></Row>
          </Container>
        );
      default:
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <h1>Maintenance services for Ground Support Equipment</h1>
                <p>
                  Your ground support equipment is there to keep your airport running smoothly. This will only happen if you keep your
                  machines and equipment performing at its best. This is where maintenance comes into play. When you call on Aviaco to
                  maintain your GSE, you minimise downtime. You ensure any potential issues are identified, diagnosed and corrected before
                  they become disruptive. You increase safety and performance, while reducing emissions and wear and tear. All of this
                  translates to a longer lifespan and better returns on existing equipment and an airport that functions as smoothly as
                  could be.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h2> A range of maintenance inspections for GSE</h2>
                <p>
                  Aviaco performs different scheduled maintenance services for clients. They are based on the specific recommendations for
                  your machines and ground support equipment, including the manufacturer recommendations for different brands and models.
                  However, we also adapt and add to these. This ensures your specific conditions are taken into account, whether they relate
                  to climate, legislation, environment, frequency of use, or any other important factor specific to you and your airport.
                  Our experiences refurbishing and repairing second-hand GSE has given us insight into the problems that arise without
                  adequate maintenance. We see first-hand how important it is to stop these problems from arising. Every step we take,
                  everything we check and observe, and every correction or replacement we make is carefully documented. Itâ€™s the best way to
                  set your mind at ease. We inform you of any future actions we expect may be necessary, provide targeted advice and let you
                  know when your next service visit will be due. In other words? We give you peace of mind.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h3>The A, B, C, D of GSE maintenance</h3>
                <p>
                  The specifics of a full maintenance inspection will depend on the GSE and the make, model and manufacturer requirements.
                  <br />
                  There are standard inclusions in all A, B, C and D checks:
                  <ul>
                    <li>general safety inspections;</li>
                    <li>engine compression tests;</li>
                    <li>weight tests for loaders, catering and ambulift trucks and other GSE;</li>
                    <li>safety inspections for lifts and scissor lifts;</li>
                    <li>air-conditioning and cooling inspections;</li>
                    <li>battery regeneration tests.</li>
                  </ul>
                </p>
              </Col>
            </Row>
            <hr />

            <Row>
              <Col>
                <h3>Professionals in GSE maintenance</h3>
                <p>
                  Aviaco maintains two major workshops for maintenance in Spain and the Netherlands, allowing us to serve our clients from
                  across Europe. However, our services are available around the world. We will gladly send a member from our team to assist,
                  whether this means performing the maintenance ourselves or training you and your team. We have also developed an extensive
                  network of experts. If your needs match with their areas of expertise, we will arrange for a local member of our network
                  to assist you. For minor maintenance tasks, we are happy to assist you and your team remotely. Itâ€™s best to contact us by
                  email or phone to arrange an appointment with the relevant expert using Skype, Teams or whatever form of communication you
                  prefer.
                </p>
              </Col>
            </Row>
            <Row>
              <Col>
                <h3>Questions about refurbished ground support equipment?</h3>
                <p>
                  Make sure you follow daily, weekly, monthly and yearly maintenance schedules and youâ€™ll get the most out of your ground
                  support equipment. Youâ€™ll find a variety of checklists available for download from our website. Would you like to know
                  more about our maintenance services for ground support equipment? Are you looking for spare parts or advice? Contact us
                  today. We are always happy to help.
                </p>
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'maintenance-seo-title' })}
        description={intl.formatMessage({ id: 'maintenance-seo-description' })}
        keywords={intl.formatMessage({ id: 'maintenance-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="maintenance" />
                  <h1 className="page-title">
                    <FormattedMessage id="maintenance" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="maintenance-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["firm", "services", "commitment"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default ContactPage;
